import { Link } from "react-router-dom";
import page404 from './../../images/page404v2.gif';

function Page404() {
    return (
        <>
            <style>{"\
        body{\
          overflow:hidden;\
        }\
      "}</style>

            <Link to="/">
                <div style={{
                    display: 'flex',
                    justifyContent: 'center',
                    height: '100vh',
                    // backgroundColor: 'black',
                    // alignItems: 'center'
                }}>
                    <img
                        title="  "
                        style={{
                            maxWidth: '100%',
                            maxHeight: '60vh',
                            objectFit: 'cover',
                            // backgroundColor: 'black',
                            overflow: 'hidden'
                        }}
                        alt={'loading...'}
                        src={page404} />
                </div>
            </Link>
        </>
    );
}

export default Page404;